@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@0;1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display:ital@0;1&family=Public+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  /*  CCOLORS  */
  --dark-pink: #ba4270;
  --light-pink: #da6d97;
  --water-white: #fbfcfe;
  --blue-dark: #36536b;
  --blue-light: #edf3f8;
  --blue-medium: #d2dbe3;
  --dark-gray-2: #1b262f;
  --light-blue-2: #6c8294;
  --disabed: #92a3b2;

  /*  FONTS  */
  --font-dm: "DM Serif Display", serif;
  --font-ps: "Public Sans", sans-serif;

  /*  SIZE  */
  --screen-size: 1160px;
  --m-horizontal-small: 2.5rem;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}
body {
  background-color: #edf3f8;
  background-color: var(--blue-light);
}

.primary-heading-big {
  font-family: "DM Serif Display", serif;
  font-family: var(--font-dm);
  font-size: 7.2rem;
}
.primary-heading-small {
  font-family: "DM Serif Display", serif;
  font-family: var(--font-dm);
  font-size: 5.6rem;
}
.secundary-heading {
  font-family: "DM Serif Display", serif;
  font-family: var(--font-dm);
  font-size: 4.8rem;
}
.tetiary-heading-big {
  font-family: "DM Serif Display", serif;
  font-family: var(--font-dm);
  font-size: 3.2rem;
}
.tetiary-heading-small {
  font-family: "Public Sans", sans-serif;
  font-family: var(--font-ps);
  font-size: 1.8rem;
}
.forth-headingl {
  font-family: "DM Serif Display", serif;
  font-family: var(--font-dm);
  font-size: 2.4rem;
}

.header {
  /*background-color: rgb(208, 218, 231);*/
  position: fixed;
  width: 100%;
  z-index: 888;
  transition: all 0.4s ease;
}
.header--animation {
  background-color: var(--blue-light);
}
.header__container {
  max-width: var(--screen-size);
  margin: 0 auto;
}
.header__wrapper {
  margin: 0 var(--m-horizontal-small);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4rem 0 2rem 0;
  transition: all 0.4s ease;
}
.header__wrapper--animation {
  padding: 2rem 0 2rem 0;
}

.header__nav {
  display: none;
}
.header__logo {
  width: 13.5rem;
}
.header__list {
  margin: 0;
  padding: 0;
}
.header__item {
  list-style: none;
}
.header__item a {
  text-decoration: none;
}
.header__menu {
  font-size: 3rem;
  color: var(--blue-dark);
}
.header__burger {
  cursor: pointer;
}
.header__btn {
  display: none;
}
@media (min-width: 425px) {
}
@media (min-width: 768px) {
  .header__nav {
    display: block;
    flex: 9 0 auto;
  }
  .header__list {
    display: flex;
    justify-content: center;
  }
  .header__item {
    list-style: none;
  }
  .header__item a {
    font-family: var(--font-ps);
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--light-blue-2);
    padding: 2rem 3rem;
    transition: all 0.8s ease;
  }
  .header__item a:hover {
    color: var(--blue-dark);
  }
  .header__burger {
    display: none;
  }
  .header__btn {
    display: block;
    flex: 2 0 auto;
    text-align: right;
  }
  .header__logo--box {
    flex: 1 0 auto;
  }
}

@media (min-width: 1024px) {
  .header__list {
    display: flex;
    justify-content: flex-start;
  }
}

.s-header__nav {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 999;
  width: 80%;
  height: 100vh;
  padding: 4rem 2.7rem;
  background-color: var(--dark-gray-2);
  background-image: url(/pay-api/static/media/circule.bf32dc38.svg);
  background-position: center -30rem;
  background-repeat: no-repeat;
  transform: translateX(100%);
  transition: all 0.8s ease;
}
.s-header__close {
  text-align: right;
  padding-bottom: 1rem;
  border-bottom: solid 1px #404b56;
  margin-bottom: 3rem;
}
.s-header__x {
  display: inline-block;
  cursor: pointer;
}
.s-header__menu {
  color: white;
  font-size: 4rem;
}
.s-header__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
}
.s-header__item {
  list-style: none;
}
.s-header__item a {
  text-decoration: none;
  display: inline-block;
  font-family: var(--font-ps);
  color: #b8bcc0;
  font-weight: 700;
  font-size: 2rem;
  padding: 2rem 3rem;
}
.s-header__btn {
  margin-top: 2rem;
  list-style: none;
  width: 100%;
}
.s-header__btn a {
  display: inline-block;
  width: 100%;
}

.s-header__nav--animate {
  transform: translateX(0);
}

@media (min-width: 425px) {
  .s-header__btn {
    margin-top: 2rem;
    text-align: center;
  }
  .s-header__btn a {
    display: inline-block;
    width: auto;
  }
}
@media (min-width: 768px) {
}

.btn {
  background-color: var(--dark-pink);
  border-radius: 10rem;
  text-decoration: none;
  font-family: var(--font-ps);
  color: var(--water-white);
  font-weight: 700;
  font-size: 1.5rem;
  padding: 1.5rem 4rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.5s ease;
  box-shadow: 10px 10px 48px -20px rgba(54, 83, 107, 1);
}

.btn:hover {
  background-color: var(--light-pink);
}

.form__group {
  width: 100%;
  text-align: center;
}
.form__input {
  border: none;
  display: block;
  width: 100%;
  font-weight: 700;
  font-size: 1.5rem;
  padding: 1.5rem 2rem;
  border-radius: 100px;
  margin-bottom: 1.5rem;
  box-shadow: 2px 15px 29px -5px rgba(56, 86, 110, 0.329);
  /*box-shadow: 40px 40px 48px -20px rgba(54, 83, 107, 1);*/
  color: var(--blue-dark);
}
.form__input::-webkit-input-placeholder {
  opacity: 0.5;
}
.form__input:-ms-input-placeholder {
  opacity: 0.5;
}
.form__input::placeholder {
  opacity: 0.5;
}
.form__btn {
  background-color: var(--dark-pink);
  border: none;
  border-radius: 10rem;
  text-decoration: none;
  font-family: var(--font-ps);
  color: var(--water-white);
  font-weight: 700;
  font-size: 1.5rem;
  padding: 1.5rem 2rem;
  text-align: center;
  cursor: pointer;
  width: 100%;
  transition: all 0.5s ease;
}
.form__btn:hover {
  background-color: var(--light-pink);
}
@media (min-width: 425px) {
  .form__group {
    width: 70%;
    text-align: center;
    margin: 0 auto;
  }
}

@media (min-width: 768px) {
  .form {
    width: 65%;
    margin: auto;
  }
  .form__group {
    width: 100%;
    text-align: left;
    margin: 0 auto;
    background-color: white;
    padding-left: 2.5rem;
    border-radius: 100px;
    display: flex;
    justify-content: space-between;
    box-shadow: 2px 15px 29px -5px rgba(56, 86, 110, 0.2);
  }
  .form__input {
    display: inline-block;
    width: 60%;
    padding: 0;
    border-radius: 0;
    margin: 0;
    box-shadow: 2px 15px 29px -5px rgba(56, 86, 110, 0);
    outline: transparent;
    background-color: transparent;
  }
  .form__btn {
    width: auto;
    display: inline-block;
  }
}

@media (min-width: 1024px) {
  .left {
    margin-left: 0;
  }
  .right {
    margin-right: 0;
  }
  .form {
    width: 85%;
    transition: all 0.6s ease;
  }
  .form__group {
    width: 100%;
    margin: 0;
  }

  .form:hover {
    width: 87%;
  }
}

