.features {
  margin-bottom: 8rem;
}
.features__container {
  max-width: var(--screen-size);
  margin: 5rem auto 0 auto;
}
.features__wrapper {
  margin: 0 var(--m-horizontal-small);
}
.features__row:not(:last-child) {
  margin-bottom: 4rem;
}
.feature--img {
  max-width: 100%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2rem;
  -webkit-filter: drop-shadow(25px 25px 80px rgba(54, 83, 107, 0.3));
          filter: drop-shadow(25px 25px 80px rgba(54, 83, 107, 0.3));
}
.features__title {
  font-size: 3.6rem;
  text-align: center;
  color: var(--blue-dark);
  margin-bottom: 2rem;
}
.features__copy {
  font-size: 1.5rem;
  font-family: var(--font-ps);
  line-height: 3rem;
  color: var(--light-blue-2);
  text-align: center;
}
@media (min-width: 768px) {
  .feature--img {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  .features__copy {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 1024px) {
  .features {
    background-image: url(/pay-api/static/media/circule.bf32dc38.svg);
    background-repeat: no-repeat;
    background-position-x: 130%;
    background-position-y: bottom;
    background-size: 70rem;
  }
  .features__row {
    display: flex;
    align-items: center;
  }
  .features__box {
    width: 50%;
  }
  .features__box .features__title--1,
  .features__box .features__title--2 {
    text-align: left;
  }
  .features__box .features__copy--1,
  .features__box .features__copy--2 {
    text-align: left;
    margin: 0;
  }
  .features__wrapper .features__row:last-child .features__box:last-child {
    order: 1;
  }
  .features__wrapper .features__row:last-child .features__box:first-child {
    order: 2;
  }
  .feature--img {
    display: inline-block;
  }
}

@keyframes spinAround {
  0% {
    transform: scale(0);
  }

  60% {
    transform: scale(1.1);
  }
  80% {
    transform: scale(1);
  }
  100% {
    transform: scale(1) translateX(-40%);
  }
}
@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

