.contact {
  padding-top: 13rem;
}
.contact__container {
  max-width: var(--screen-size);
  margin: 0 auto;
}
.contact__wrapper {
  margin: 0 var(--m-horizontal-small);
}
.contact__title {
  text-align: center;
  font-family: var(--font-dm);
  font-size: 3.2rem;
  font-weight: 500;
  color: var(--blue-dark);
  margin-bottom: 5rem;
}
.f-group {
  width: 100%;
  margin-bottom: 0rem;
}
.f-group span {
  padding-top: 0.5rem;
  display: block;
  padding: 1rem;
  font-size: 1.1rem;
  font-family: var(--font-ps);
  color: #ff0000;
}
.contact__form input,
textarea {
  width: 100%;
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #36536b;
  padding: 1rem;
  font-family: var(--font-ps);
  font-size: 1.5rem;
  outline: transparent;
  resize: none;
}
input::-webkit-input-placeholder {
  -webkit-transition: all 0.6s ease;
  transition: all 0.6s ease;
}
input:-ms-input-placeholder {
  -ms-transition: all 0.6s ease;
  transition: all 0.6s ease;
}
input::placeholder {
  transition: all 0.6s ease;
}
.contact__form input:focus::-webkit-input-placeholder {
  font-size: 1.2rem;
}
.contact__form input:focus:-ms-input-placeholder {
  font-size: 1.2rem;
}
.contact__form input:focus::placeholder {
  font-size: 1.2rem;
}
.c-error {
  border-bottom: solid 1px #ff0000 !important;
  color: #ff0000 !important;
}
.c-error::-webkit-input-placeholder {
  color: #ff0000 !important;
}
.c-error:-ms-input-placeholder {
  color: #ff0000 !important;
}
.c-error::placeholder {
  color: #ff0000 !important;
}
.contact__logo {
  margin-top: 5rem;
}

.contact__check-group {
  display: flex;
}
.contact__check-group .checkbox {
  width: auto;
  margin-right: 1rem;
}
.contact__check-group label {
  font-family: var(--font-ps);
  font-size: 1.5rem;
  color: var(--blue-dark);
}
.contact__logo-title {
  font-size: 2.5rem;
  font-family: var(--font-ms);
  text-align: center;
  margin-bottom: 3rem;
  color: var(--disabed);
  line-height: 4rem;
}
.contact__logos {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.contact__logos img {
  padding: 2rem;
  justify-self: center;
  align-self: center;
  width: 90%;
}

@media (min-width: 768px) {
  .contact__title {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }
  .contact__form {
    width: 70%;
    margin: 0 auto;
  }
  .contact__logos {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .contact__logos > img {
    width: 70%;
  }
  .contact__logo-title {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 1024px) {
  .contact__content {
    display: flex;
    justify-content: space-between;
  }
  .contact__title {
    width: 50%;
    margin-left: 0;
    text-align: left;
  }
  .contact__form {
    width: 100%;
    margin: 0 auto;
  }
  .contact__logos {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
  }
  .contact__logos > img {
    width: 70%;
    justify-self: flex-start;
    padding: 1rem 0;
  }
  .contact__logo-title {
    width: 70%;
    margin-left: 0;
    text-align: left;
  }
  .c-group {
    width: 40%;
  }
  .contact__logo {
    width: 50%;
    margin-top: 0rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
  }
}

